import React, { useState } from "react";
import { Trans } from "@lingui/react/macro";
import { supabase } from "../../lib/supabaseClient"; // Asegúrate de tener tu cliente Supabase configurado

function Participa() {
  const [form, setForm] = useState({
    name: "",
    email: "",
    message: "",
    profile: "",
    keepInformed: false,
    honeypot: "",
  });

  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm({
      ...form,
      [name]: type === "checkbox" ? checked : value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Prevención de spam
    if (form.honeypot) return;

    // Validación simple
    if (!form.name || !form.email || !form.message || !form.profile) {
      alert("Por favor completa todos los campos requeridos.");
      return;
    }

    setLoading(true);

    // Inserción en Supabase
    const { data, error } = await supabase.from("participantes").insert([
      {
        nombre: form.name,
        email: form.email,
        contribucion: form.message,
        perfil: form.profile,
      },
    ]);

    setLoading(false);

    if (error) {
      console.error("Error al guardar en la base de datos:", error);
      alert("Ocurrió un error. Intenta nuevamente.");
    } else {
      console.log("Formulario guardado:", data);
      setSuccess(true);
      setForm({
        name: "",
        email: "",
        message: "",
        profile: "",
        honeypot: "",
      });
    }
  };

  return (
    <div className="min-h-fit flex flex-col justify-center items-center bg-cyan-600 text-white mb-12">
      <h1 className="font-bold mb-10 text-center text-4xl md:text-5xl xl:text-6xl 3xl:text-7xl">
        <Trans>JOIN US</Trans>
      </h1>
      <form
        onSubmit={handleSubmit}
        className="w-full max-w-2xl flex flex-col gap-6 bg-white text-black p-8 mb-12 rounded-lg shadow-lg font-helvetica "
      >
        <input
          type="text"
          name="name"
          placeholder="Nombre"
          value={form.name}
          onChange={handleChange}
          className="w-full p-3 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-yellow-400"
          required
        />
        <input
          type="email"
          name="email"
          placeholder="Correo"
          value={form.email}
          onChange={handleChange}
          className="w-full p-3 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-yellow-400"
          required
        />
        <textarea
          name="message"
          placeholder="Propuestas (máx. 300 caracteres)"
          maxLength={300}
          value={form.message}
          onChange={handleChange}
          className="w-full p-3 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-yellow-400"
          rows={4}
          required
        />
        <input
          type="text"
          name="honeypot"
          style={{ display: "none" }}
          value={form.honeypot}
          onChange={handleChange}
        />
        <select
          name="profile"
          value={form.profile}
          onChange={handleChange}
          className="w-full p-3 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-yellow-400"
          required
        >
          <option value="">
            <Trans>Select a profile</Trans>
          </option>
          <option value="estudiante">
            <Trans>Student</Trans>
          </option>
          <option value="pdi">PDI</option>
          <option value="psa">PTGAS</option>
          <option value="otro">
            <Trans>Other</Trans>
          </option>
        </select>

        {/* Botón enviar */}
        <button
          type="submit"
          disabled={loading}
          className="px-6 py-3 bg-yellow-400 text-blue-700 font-semibold rounded-lg shadow-md hover:bg-yellow-300 transition disabled:opacity-50"
        >
          {loading ? "Enviando..." : <Trans>Send</Trans>}
        </button>

        {success && (
          <p className="text-green-600 font-semibold mt-4">
            <Trans>Your contribution was successfully submited!</Trans>
          </p>
        )}
      </form>
    </div>
  );
}

export default Participa;
