import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import LandingPage from "./pages/LandingPage/LandingPage";
import MotivacionPage from "./pages/Motivation/MotivationPage";
import IntroductionPage from "./pages/Introduction/IntroductionPage";
import ProposalsPage from "./pages/Proposals/ProposalsPage";
import NewsAndSchedulePage from "./pages/NewsAndSchedule/NewsAndSchedulePage";

import { i18n } from "@lingui/core";
import { I18nProvider } from "@lingui/react";
import { messages as enMessages } from "./locales/en/messages";
import { messages as esMessages } from "./locales/es/messages";

const locale = "es";

const messages = {
  en: enMessages,
  es: esMessages,
};

i18n.load(locale, messages[locale]);
i18n.activate(locale);

function App() {
  return (
    <I18nProvider i18n={i18n}>
      <Router>
        <Routes>
          <Route path="/" element={<LandingPage />} />
          <Route path="/motivacion" element={<MotivacionPage />} />
          <Route path="/introduccion" element={<IntroductionPage />} />
          <Route path="/programa" element={<ProposalsPage />} />
          <Route path="/noticias" element={<NewsAndSchedulePage />} />
          <Route path="*" element={<LandingPage />} />
        </Routes>
      </Router>
    </I18nProvider>
  );
}

export default App;
