import React, { useEffect, useState } from "react";
import { Trans } from "@lingui/react/macro";
import { supabase } from "../../lib/supabaseClient";
import { Link } from "react-router-dom";

function Agenda() {
  const [entries, setEntries] = useState([]);

  useEffect(() => {
    async function fetchEntries() {
      const { data, error } = await supabase
        .from("AgendaYNoticias")
        .select("*")
        .order("id", { ascending: false }) // últimas primero
        .limit(3);

      if (error) {
        console.error("Error fetching AgendaYNoticias:", error);
      } else {
        setEntries(data);
      }
    }
    fetchEntries();
  }, []);

  const truncateText = (text, length = 150) => {
    if (!text) return "";
    if (text.length <= length) return text;
    return text.slice(0, length) + "... ";
  };

  return (
    <div className="min-h-fit flex flex-col items-center bg-white text-black">
      <h1 className="font-bold mt-12 mb-10 text-center text-4xl md:text-5xl xl:text-6xl 3xl:text-7xl">
        <Trans>NEWS AND SCHEDULE</Trans>
      </h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-6xl justify-center">
        {entries.map((item) => (
          <Link
            key={item.id}
            to={`/noticias?selectedId=${item.id}`}
            className="bg-gray-100 rounded-xl shadow-md overflow-hidden flex flex-col w-80 transform transition-transform duration-300 hover:scale-105"
          >
            {/* Línea superior según tipo */}
            <div
              className={`h-2 ${item.tipo === "Agenda" ? "bg-yellow-400" : "bg-cyan-400"}`}
            />

            {item.imagen && (
              <img
                src={item.imagen}
                alt={item.titulo}
                className="w-full h-48 object-cover"
              />
            )}

            <div className="p-6 flex flex-col flex-grow">
              <span className="text-gray-500 text-sm mb-2">{item.tipo}</span>
              <h2 className="text-2xl font-semibold mb-0">{item.titulo}</h2>
              <span className="text-gray-500 text-sm mb-0">{item.fecha}</span>
              <span className="text-gray-500 text-sm mb-2">
                {item.hora ? item.hora.slice(0, 5) : ""}
              </span>
              <p className="text-gray-700 flex-grow">
                <span
                  dangerouslySetInnerHTML={{ __html: truncateText(item.texto, 50) }}
                />
                <span className="text-blue-600 font-medium hover:underline">
                  LEER MÁS
                </span>
              </p>
            </div>
          </Link>
        ))}
      </div>

      <div className="mt-12 mb-12">
        <Link
          to="/noticias"
          className="md:text-md xl:text-lg 3xl:text-2xl px-6 py-4 bg-yellow-400 text-blue-700 font-bold rounded-lg shadow-md hover:bg-yellow-300 transition"
        >
          <Trans>READ MORE</Trans>
        </Link>
      </div>
    </div>
  );
}

export default Agenda;
