/*eslint-disable*/module.exports={messages:JSON.parse("{\"mVMnD2\":[\" DISCOVER OUR PLATFORM \"],\"HK5Fh0\":[\"<0>Because another way of governing the university is possible.</0> The style of governance at the University of Seville can and must be improved. I will lead the University in a more agile and approachable way. More can be done with less—especially with fewer positions. I am committed to effective, efficient, and transparent management of resources.\"],\"PjPs26\":[\"<0>Because I will implement a system of co-governance with the schools and true decentralization.</0> Faculties, Schools, Departments, and Institutes carry out the University’s core activities and will have broad autonomy to do so, but always with oversight and accountability.\"],\"zZqEr2\":[\"<0>Because the Rector will listen to you (and respond).</0> The Rector must foster dialogue at every level, not impose a monologue. She must listen to and serve the entire university community.\"],\"1CJe65\":[\"<0>Because the team will be you. The Rector wants to count on everyone to build the University of the future.</0> There are no positions or team yet, because we still need to agree together on what we want the streamlined structure of the University to be.\"],\"VCwa1w\":[\"<0>Because we are all the University of Seville.</0> We must draw on the talent and experience of the entire university community and begin to govern from the bottom up and from the periphery to the center. Because decisions should be made by those who know. Decisions must be reached through agreements, with the highest possible level of consensus, and with the participation of those who truly understand each issue as well as those who are directly affected.\"],\"yktLOT\":[\"<0>THE UNIVERSITY TO COME... IS YOURS TO MAKE</0>\"],\"fXxqy3\":[\"A Letter to the University Community\"],\"447C14\":[\"A student who faced difficulties and uncertainties until I graduated in Medicine two years ago. A professor who lives with the excitement of seeing a student learn something new for the first time, and who feels overwhelmed by changes in procedures and IT systems. A researcher who is amazed and thrilled with every discovery, yet afraid a project report might be submitted incorrectly. A worker at the University of Seville who has had no patrons. My only concern is people like you and me. I have no political or other interests to defend. I am proud to belong to and feel part of the University of Seville. I will be a different kind of Rector; I will be like you.\"],\"+Xzu3k\":[\"A student who faced difficulties and uncertainties until I graduated in Medicine two years ago...\"],\"5WT06W\":[\"ABOUT ME\"],\"sGxHeV\":[\"All rights reserved\"],\"xhQj64\":[\"Although I am still young, I have spent nearly 30 years at the University of Seville.I am a Professor of Business Organization.I hold a degree in Economics and Business Studies, a degree in Medicine, and a PhD in Management and Business Administration.I have management experience in the banking sector and have also been involved in university administration.And I believe my work as a professor and researcher is the best job in the world, and one of the greatest things that has happened to me in life.\"],\"p0KEeR\":[\"And I am like you.\"],\"0bAvzz\":[\"Because another style of governing the University of Seville is possible. Another way of being Rector is possible. Another way of speaking to each other, listening to each other, and making decisions together is possible. You can make it possible. I need you to become Rector, but when I will truly need you is once I am Rector. If we all do this—and my team and I will be the first to do so—we will achieve a better University, where we feel better, where we do our work (whatever it may be) better, and where we find the best place to learn, to graduate, to research, and to work.\"],\"TV+uAx\":[\"Because another style of governing the University of Seville is possible. Another way of being Rector is possible. Another way of speaking to each other, listening to each other, and making decisions together is possible. You can make it possible. I need you to become Rector, but when I will truly need you is once I am Rector. If we all do this—and my team and I will be the first to do so—we will achieve a better University, where we feel better, where we do our work (whatever it may be) better, and where we find the best place to learn, to graduate, to research, and to work.<0/><1/>We will not achieve it on the first day, but we will have six years to try together. With you, of course it is possible. From this moment, I consider you part of my team.\"],\"fBua1t\":[\"Biography\"],\"21BIFi\":[\"BIOGRAPHY\"],\"ZijJqC\":[\"CANDIDATE FOR RECTOR\"],\"jfC/xh\":[\"Contact\"],\"N/+7yd\":[\"DOWNLOAD CV\"],\"i0qMbr\":[\"Home\"],\"lvfaxv\":[\"HOME\"],\"GnLp4l\":[\"I am <0>ÁNGELES GALLEGO</0> and I want to be your <1>RECTOR</1>.\"],\"lhuUrx\":[\"I am <0>ÁNGELES GALLEGO</0> and I want to be your <1>RECTOR</1>.<2/><3/>And I am like you.<4/><5/>A student who faced difficulties and uncertainties until I graduated in Medicine two years ago. A professor who lives with the excitement of seeing a student learn something new for the first time, and who feels overwhelmed by changes in procedures and IT systems. A researcher who is amazed and thrilled with every discovery, yet afraid a project report might be submitted incorrectly. A worker at the University of Seville who has had no patrons. My only concern is people like you and me. I have no political or other interests to defend. I am proud to belong to and feel part of the University of Seville. I will be a different kind of Rector; I will be like you.<6/> <7/>Although I am still young, I have spent nearly 30 years at the University of Seville. I am a Professor of Business Organization. I hold a degree in Economics and Business Studies, a degree in Medicine, and a PhD in Management and Business Administration. I have management experience in the banking sector and have also been involved in university administration. And I believe my work as a professor and researcher is the best job in the world, and one of the greatest things that has happened to me in life.\"],\"u97/c7\":[\"I am <0>ÁNGELES GALLEGO</0> and I want to be your <1>RECTOR</1>.<2/><3/>And I am like you.<4/><5/>A student who faced difficulties and uncertainties until I graduated in Medicine two years ago. A professor who lives with the excitement of seeing a student learn something new for the first time, and who feels overwhelmed by changes in procedures and IT systems. A researcher who is amazed and thrilled with every discovery, yet afraid a project report might be submitted incorrectly. A worker at the University of Seville who has had no patrons. My only concern is people like you and me. I have no political or other interests to defend. I am proud to belong to and feel part of the University of Seville. I will be a different kind of Rector; I will be like you.<6/><7/>Although I am still young, I have spent nearly 30 years at the University of Seville. I am a Professor of Business Organization. I hold a degree in Economics and Business Studies, a degree in Medicine, and a PhD in Management and Business Administration. I have management experience in the banking sector and have also been involved in university administration. And I believe my work as a professor and researcher is the best job in the world, and one of the greatest things that has happened to me in life.\"],\"qdJ1m1\":[\"I am <0>ÁNGGALLEGO</0> and I want to be your <1>RECTOR</1>.\"],\"ljhnNe\":[\"I take a step forward with the strength of all the people who share with me the need to build the University that this moment demands, and with the clear objective of leading a new US. This is a project we must build together, and I invite every member of the university community to take part in it. The doors of my candidacy are open to everyone, because <0>“The University to come… is yours to make</0>.”\"],\"lbjoFH\":[\"I want a University that serves people. We must change the culture of the institution so that health, respect, equality, and ethics are part of our daily work. I will fight for the people of the University of Seville to feel happy and engaged with it. I will seek spaces for affection and laughter. And we will approach everything (or almost everything) with a sense of humor. And I hope to count on you, because the University to come… is made by you.\"],\"65y/l6\":[\"I WANT TO BE YOUR RECTOR\"],\"398dbL\":[\"I want to be your Rector because a group of people from different backgrounds encouraged me to do so, and I have gathered a team of individuals willing to contribute after listening to members from various communities. Those of us conforming this team are ordinary people, without aspirations of holding positions of power, generously adding extra work, time, and effort towards the betterment of our University and its community. We are people just like you. And, just like you, we show up each day to make the University work.\"],\"u+/OOb\":[\"Introduction\"],\"VuZrNd\":[\"INTRODUCTION\"],\"biwvFm\":[\"JOIN US\"],\"O0Y+Sj\":[\"Keep me up to date.\"],\"5+jQOp\":[\"LA UNIVERSIDAD QUE VIENE, <0/> LA HACES TÚ\"],\"4s8rMJ\":[\"LOOK UP WHAT WE CAN ACHIEVE TOGETHER\"],\"9hajs/\":[\"MEET THE CANDIDATE\"],\"amn0yp\":[\"Motivation\"],\"3oUXzh\":[\"MY MOTIVATION\"],\"kylqyr\":[\"News & Schedule\"],\"9YliER\":[\"NEWS AND SCHEDULE\"],\"CovYpP\":[\"Now is the time to do all of this. We must seize the opportunity of these elections, with their new format, to establish a new style of relationships and governance in our University.\"],\"hESo/4\":[\"OR EXPLORE\"],\"/IX/7x\":[\"Other\"],\"kN6Wkt\":[\"OUR PROGRAM\"],\"gaCyZ3\":[\"PARTICIPA\"],\"QftTdJ\":[\"PDI\"],\"iCdVLj\":[\"PRESENTATION\"],\"6g1gi0\":[\"Proposals\"],\"v1z8kN\":[\"PROPOSALS\"],\"Ce92BF\":[\"PTGAS\"],\"72boRg\":[\"QUIERO SER TU RECTORA\"],\"gR3ouv\":[\"READ MORE\"],\"8mDLgw\":[\"RETURN\"],\"WbozdU\":[\"SCHEDULE\"],\"3+MXzC\":[\"Select a profile\"],\"SUrLL9\":[\"Select a profile to filter the proposals that best suit your needs.\"],\"JlFcis\":[\"Send\"],\"ZR57vN\":[\"Sincerely,\"],\"51mRUj\":[\"Society is going through a period of uncertainties, and the role of science in addressing it is often called into question. At the same time, the challenges facing the public sphere are compounded by the fact that private universities are beginning to gain ground on us. We must wake up and strive to provide a swift and effective response to the problems that arise.\"],\"EVV8/3\":[\"Soy <0>ÁNGELES GALLEGO</0> y quiero ser tu <1>RECTORA</1>.<2/><3/>Y soy como tú.<4/><5/>Una estudiante con las dificultades y las incertidumbres hasta que me gradué en Medicina hace dos años. Una profesora que vive con la ilusión del primer día que un alumno aprenda algo nuevo y que se agobia con los cambios en los procedimientos y en las aplicaciones informáticas. Una investigadora que se sorprende y se entusiasma con cada descubrimiento y vive con miedo a que una liquidación de un proyecto esté mal hecha. Una trabajadora de la Universidad de Sevilla que no ha tenido padrinos. Solo me preocupa la gente como tú y como yo.<6/><7/>No tengo ningún interés político ni de otro tipo que defender. Estoy orgullosa de pertenecer y de sentirme una parte de la Universidad de Sevilla. Seré una Rectora diferente, seré como tú. Aunque todavía soy joven, llevo casi 30 años en la Universidad de Sevilla. Soy Catedrática de Organización de Empresas. Licenciada en Ciencias Económicas y Empresariales, Graduada en Medicina y Doctora en Administración y Dirección. He tenido experiencia directiva en el sector bancario y me he ocupado también de la gestión universitaria. Y pienso que mi trabajo, el de profesora e investigadora, es el mejor del mundo y una de las mejores cosas que me han pasado en la vida.\"],\"a0qEkS\":[\"Student\"],\"WKOJ7q\":[\"STUDENT\"],\"gjANha\":[\"The Rectorate, in addition to the role of the Rector, who must lead and take responsibility for the entire organization, is essentially where all the support services for the University’s two core missions <0>research and teaching</0> are concentrated. Some call them central services. But my vision is of a more decentralized University of Seville. These central services must be resized and adapted in order to properly fulfill their functions at any given time. This requires a significant degree of flexibility. The structure of the University of Seville’s governance during my term will be (re)designed regularly to respond to change, to the demands of our environment, and to the strategic challenges we set for ourselves together. In this way, we aim to provide certainty and to safeguard the public good.\"],\"R85k7N\":[\"The Rectorate, in addition to the role of the Rector, who must lead and take responsibility for the entire organization, is essentially where all the support services for the University’s two core missions—research and teaching—are concentrated. Some call them central services. But my vision is of a more decentralized University of Seville. These central services must be resized and adapted in order to properly fulfill their functions at any given time. This requires a significant degree of flexibility. The structure of the University of Seville’s governance during my term will be (re)designed regularly to respond to change, to the demands of our environment, and to the strategic challenges we set for ourselves together. In this way, we aim to provide certainty and to safeguard the public good.\"],\"cmAJeO\":[\"THE UNIVERSITY TO COME, <0/> IS YOURS TO MAKE\"],\"OwDkQv\":[\"THE UNIVERSITY TO COME,<0/>IS YOURS TO MAKE\"],\"dHzWRg\":[\"THE UNIVERSITY TO COME...<0/>IS YOURS TO MAKE\"],\"6BMzyL\":[\"THE UNIVERSITY WE DESERVE<0/>STARTS WITH YOU\"],\"MdcSRc\":[\"We will not achieve it on the first day, but we will have six years to try together. With you, of course it is possible. From this moment, I consider you part of my team.\"],\"KBB8H+\":[\"Y es que otro estilo de gobierno de la Universidad de Sevilla es posible. Otra forma de ser Rectora es posible. Otra forma de hablarnos, de escucharnos y de decidir juntos es posible. Tú lo puedes hacer posible. Te necesito para ser Rectora, pero cuando realmente te voy a necesitar es cuando sea Rectora. Si todos lo hacemos, y mi equipo y yo seremos los primeros en hacerlo, conseguiremos una Universidad mejor, donde nos sintamos mejor, donde hagamos mejor nuestro trabajo (el que sea) y donde encontremos el mejor sitio para aprender, para titularnos, para investigar y para trabajar.<0/><1/>No lo vamos a lograr el primer día, pero tendremos seis años para intentarlo juntos. Contigo claro que es posible. Desde este momento te considero miembro de mi equipo.\"],\"iE1Bur\":[\"Your contribution was successfully submited!\"]}")};