import React, { useRef, useState, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import { Helmet } from "react-helmet";
import Navbar from "../../components/Navbar";
import Footer from "../../components/Footer";
import AgendaYNoticias from "./AgendaYNoticias";
import Divider from "../../components/Divider";

function NewsAndSchedulePage() {
  const nodeRef = useRef(null);
  const [show, setShow] = useState(false);

  useEffect(() => {
    setShow(true);
  }, []);

  return (
    <div className="bg-black">
      <Helmet>
        <title>Noticias y agenda | Ángeles Gallego candidata a Rectora US</title>
        <meta
          name="description"
          content="Sigue las últimas noticias y eventos de mi candidatura a Rectora de la Universidad de Sevilla. Participa, infórmate y construyamos juntos la Universidad que viene."
        />
        <meta
          name="keywords"
          content="elecciones universidad de Sevilla, agenda universidad de Sevilla, elecciones rectorado universidad de Sevilla, eventos candidatura ángeles gallego, actividades universidad de Sevilla" />
      </Helmet>
      <CSSTransition
        in={show}
        timeout={300}
        classNames="fade"
        unmountOnExit
        nodeRef={nodeRef}
      >
        <div ref={nodeRef} className="font-serif text-gray-900">
          <Navbar />
          <main>
            <Divider height="96px" />
            <section id="noticias" className="scroll-mt-48">
              <AgendaYNoticias />
            </section>
          </main>
          <Divider height="96px" />
          <Footer />
        </div>
      </CSSTransition>
    </div>
  );
}

export default NewsAndSchedulePage;
